import authService from "@/services/user/auth.service";

export default {  
  name: "Header",  
  data() {
    return {
      openMenu: false,
      showNavMobile: false,
      isLogged: false,
      user_name: ''
    };
  },
  created() {
    const token = authService.getClientToken()
    this.user_name = token.client.name
  },  
  methods: {
    async logout() {
      try {
          // Llamando al API para desloguearse //     
          const {status, data} = await authService.checkLogin(`/clients/logout`, {auth: 'bearer'})
          if(status == 200) {
              sessionStorage.setItem('token', data.token)              
              authService.destroyLocalUserData()
              if (this.$route.fullPath != 'Login') {
                this.$router.push({name: 'Login'})
              }
          } else {
              console.log('problemas al desloguearse')
          }
      } catch(e) {                
          console.log(e)
      }
    }
  },
};
